import React from 'react';
import { CurrentUserContext } from '../contexts/CurrentUserContext';

function Card(props) {
  const currentUser = React.useContext(CurrentUserContext);

  const isOwn = props.card.owner._id === currentUser._id;

  const isLiked = props.card.likes.some(i => i._id === currentUser._id);

  function handleClick() {
    props.onCardClick(props.card);
  } 
  
  function handleLikeClick() {
    props.onCardLike(props.card)
  }

  function handleDeleteClick() {
    props.onCardDelete(props.card)
  }

  return(
  <li className="card">
    {isOwn ? <button className="button-delete" type="button" onClick={handleDeleteClick}></button> : ''}
    <div className="card__image" onClick={handleClick} style={{ backgroundImage: `url(${props.card.link})`, backgroundSize: 'cover', backgroundPosition: 'center'}}/>
    <div className="card__group">
      <h3 className="card__title">{props.card.name}</h3>
      <div className="card__like-group"> 
        <button className={`button-like ${isLiked ? 'button-like_active' : ''}`} type="button" onClick={handleLikeClick}></button>
        <p className="button-like__counter">{props.card.likes.length}</p>
      </div>
    </div>
  </li>
  )
}

export default Card;
