function PopupWhithForm (props) {
  return (
  <div className={`popup popup_type_${props.name} ${props.isOpen ? 'popup_opened' : ''}`}>
    <form className={`popup__container popup__container_type_${props.name}`} onSubmit={props.onSubmit} action="#" method="#" name={props.name}noValidate>
      <button className="popup__close" type="reset" onClick={props.onClose}></button>
      <h2 className="popup__title">{props.title}</h2>
      {props.children}
      <button className="popup__save" type="submit">{props.buttonText}</button>
    </form>
  </div>
  )
}

export default PopupWhithForm;
