import PopupWhithForm from './PopopWhithForm.js';
import React from 'react';

function AddPlacePopup(props) {
  const [place, setPlace] = React.useState('');
  const [link, setLink] = React.useState('');

  function handleChangePlace(e) {
    setPlace(e.target.value);
  }

  function handleChangeLink(e) {
    setLink(e.target.value);
  }

  function handleSubmit(e) {
    e.preventDefault();

    props.onAddPlace({
      link: link, 
      name: place})

    setLink('');
    setPlace('');
  }

  return (
    <PopupWhithForm title="Новое место" onSubmit={handleSubmit} name="add-card" buttonText="Сохранить" isOpen={props.isOpen} onClose={props.onClose}>
      <div className="popup__inputs">
        <label className="popup__label">
          <input className="popup__field popup__field_value_place" value={place} onChange={handleChangePlace} id="popup__field_place" type="text" name="name" placeholder="Название" minLength="2" maxLength="30" required />
          <span className="popup__field-error" id="popup__field_place-error"></span>
        </label>
        <label className="popup__label">
          <input className="popup__field popup__field_value_url" value={link} onChange={handleChangeLink} id="popup__field_url" type="url" name="link" placeholder="Ссылка на картинку" required />
          <span className="popup__field-error" id="popup__field_url-error"></span>
        </label>  
      </div>
    </PopupWhithForm>
  )
}

export default AddPlacePopup;
