function ImagePopup(props) {
return (
  <div className={`popup popup_type_image-viewer ${props.isOpen}`}>
    <div className="popup__image-viewer">
      <button className="popup__close" type="reset" onClick={props.onClose}></button>
      <figure className="popup__image-form">
        <img className="popup__image" src={`${props.card ? props.card.link : ''}`} alt={props.card.name}/>
        <figure className="popup__title-place">{props.card.name}</figure>
      </figure>
    </div>
  </div>
)
}

export default ImagePopup;

