import PopupWhithForm from './PopopWhithForm';
import React from 'react';
import { CurrentUserContext } from '../contexts/CurrentUserContext';

function EditProfilePopup(props) {
  const currentUser = React.useContext(CurrentUserContext);

  const [ name, setName] = React.useState('');
  const [ description, setDescription] = React.useState('');

  function handleChangeName(e) {
    setName(e.target.value);
  }

  function handleChangeDescription(e) {
    setDescription(e.target.value);
  }

  function handleSubmit(e) {
    e.preventDefault();
  
    props.onUpdateUser({
      name,
      about: description,
    });

  } 

  React.useEffect(() => {
    setName(currentUser.name);
    setDescription(currentUser.about);
  }, [currentUser]); 

  return(
      <PopupWhithForm onSubmit={handleSubmit} title="Редактировать профиль" name="edit-profile" buttonText="Сохранить" isOpen={props.isOpen} onClose={props.onClose}>
        <div className="popup__inputs">
          <label className="popup__label">
            <input className="popup__field popup__field_value_name" value={name} id="popup__field_name" onChange={handleChangeName} type="text" name="name" required minLength="2" maxLength="40" />
            <span className="popup__field-error" id="popup__field_name-error"></span>
          </label>
          <label className="popup__label">
            <input className="popup__field popup__field_value_profession" value={description} onChange={handleChangeDescription} id="popup__field_profession" type="text" name="about" required minLength="2" maxLength="200"/>
            <span className="popup__field-error" id="popup__field_profession-error"></span>
          </label>
        </div>  
      </PopupWhithForm>
  )
}

export default EditProfilePopup;
