import React from 'react';
import Card from './Card.js';
import { CurrentUserContext } from '../contexts/CurrentUserContext';

function Main(props) {

  const currentUser = React.useContext(CurrentUserContext);

  return (
    <main className="page__content">
      <section className="profile">
        <div className="profile__info">
          <div className="profile__avatar" style={{ backgroundImage: `url(${currentUser.avatar})`, backgroundSize: 'cover' }}/>
          <div className="profile__avatar-overlay" onClick={props.onEditAvatar}></div>
          <div className="profile__group">
            <div className="profile__text-block">
              <h1 className="profile__name">{currentUser.name}</h1>
              <p className="profile__profession">{currentUser.about}</p>
            </div>
            <button className="button-editor" type="button" onClick={props.onEditProfile}></button>
          </div>
        </div>
        <button className="button-add" type="button" onClick={props.onAddPlace}></button>
      </section>
      <section className="cards">
        <ul className="cards__list">
          {props.cards.map((card) => {
            return (
              <Card card={card} key={card._id} onCardDelete={props.onCardDelete} onCardClick={props.onCardClick} onCardLike={props.onCardLike} />
            )
          })}
        </ul>
      </section>
    </main>
  )
}

export default Main 
