import PopupWhithForm from './PopopWhithForm';
import React from 'react';

function EditAvatarPopup(props) {
  const inputRef = React.useRef();

  function handleSubmit(e) {
    e.preventDefault();

    props.onUpdateAvatar(inputRef.current.value);

    inputRef.current.value = '';
  }

  return (
    <PopupWhithForm onSubmit={handleSubmit} title="Обновить аватар" name="change-avatar" buttonText="Сохранить" isOpen={props.isOpen} onClose={props.onClose}>
      <div className="popup__inputs">
        <label className="popup__label">
          <input className="popup__field popup__field_value_avatar" ref={inputRef} id="popup__field_avatar" type="url" name="link" placeholder="Ссылка на картинку" required  />
          <span className="popup__field-error" id="popup__field_avatar-error"></span>
        </label>
      </div>  
    </PopupWhithForm>
  )
};

export default EditAvatarPopup;
