import React from 'react';
import Header from './Header.js';
import Main from './Main.js';
import Footer from './Footer.js'
import PopupWhithForm from './PopopWhithForm.js';
import ImagePopup from './ImagePopup.js'
import { CurrentUserContext } from '../contexts/CurrentUserContext.js';
import { api } from '../utils/api';
import EditProfilePopup from './EditProfilePopup';
import EditAvatarPopup from './EditAvatarPopup';
import AddPlacePopup from './AddPlacePopup';


function App() {

  const [isEditProfilePopupOpen, setIsEditProfilePopupOpen] = React.useState(false);
  const [isAddPlacePopupOpen, setIsAddPlacePopupOpen] = React.useState(false);
  const [isEditAvatarPopupOpen, setIsEditAvatarPopupOpen] = React.useState(false);
  const [isImagePopupOpen, setIsImagePopupOpen ] = React.useState(false);

  const [cards, setCards] = React.useState([]);
  const [selectedCard, setSelectedCard] = React.useState({name: '', link: ''});

  const [currentUser, setCurrentUser] = React.useState('');

  React.useEffect(() => {
    api.getUserData()
    .then((data) => {
      setCurrentUser(data)
    })
    .catch((err) => { console.log(`Ошибка: ${err}`)})
  }, [])

  React.useEffect(() => {
    api.getCards()
    .then((data) => {
      setCards(data);
    })
    .catch((err) => { console.log(`Ошибка: ${err}`)})
  }, [])

  function handleCardLike(card) {
    const isLiked = card.likes.some(i => i._id === currentUser._id);
    
    api.changeLikeCardStatus(card._id, !isLiked)
    .then((newCard) => {
      const newCards = cards.map((c) => c._id === card._id ? newCard : c);
      setCards(newCards);
    })
    .catch((err) => { console.log(`Ошибка: ${err}`)})
  }

  function handleCardDelete(card) {
    api.deleteCard(card._id)
    .then(() => {
      const newCards = cards.filter(c => c._id !== card._id);
      setCards(newCards);
    })
    .catch((err) => { console.log(`Ошибка: ${err}`)})
  }

  function handleCardClick(cardsInfo) {
    setIsImagePopupOpen(true);
    setSelectedCard(cardsInfo)
  }

  function handleEditProfileClick() {
    setIsEditProfilePopupOpen(true);

  }

  function handleEditAvatarClick() {
    setIsEditAvatarPopupOpen(true);
  }

  function handleAddPlaceClick() {
    setIsAddPlacePopupOpen(true);
  }

  function closeAlllPopup() {
    setIsAddPlacePopupOpen(false);
    setIsEditAvatarPopupOpen(false);
    setIsEditProfilePopupOpen(false);
    setIsImagePopupOpen(false);
  }

  function handleUpdateUser(info) {
    api.setUserData(info)
    .then((data) => {
      setCurrentUser(data);
      closeAlllPopup();
    })
    .catch((err) => { console.log(`Ошибка: ${err}`)})
  }

  function handleUpdateAvatar(link) {
    api.changeUserAvatar(link)
    .then((data) => {
      closeAlllPopup();
      setCurrentUser(data);
    })
    .catch((err) => { console.log(`Ошибка: ${err}`)})
  }

  function handleAddPlaceSubmit(card) {
    api.addCard(card)
    .then((newCard) => {
      setCards([newCard, ...cards]);
      closeAlllPopup() 
    })
    .catch((err) => { console.log(`Ошибка: ${err}`)})
  }

  return (
  <CurrentUserContext.Provider value={currentUser}>
    <div className="page">
      <div className="page__container">
        <Header />
        <Main cards={cards} onCardLike={handleCardLike} onCardDelete={handleCardDelete} onCardClick={handleCardClick} onEditProfile={handleEditProfileClick} onAddPlace={handleAddPlaceClick} onEditAvatar={handleEditAvatarClick} />
        <Footer />
      </div>

      <EditProfilePopup isOpen={isEditProfilePopupOpen} onUpdateUser={handleUpdateUser} onClose={closeAlllPopup} />
      
      <EditAvatarPopup isOpen={isEditAvatarPopupOpen} onClose={closeAlllPopup} onUpdateAvatar={handleUpdateAvatar} />

      <AddPlacePopup isOpen={isAddPlacePopupOpen} onClose={closeAlllPopup} onAddPlace={handleAddPlaceSubmit} />

      <PopupWhithForm title="Вы уверены?" name="verification" buttonText="Да"/>
      <ImagePopup card={selectedCard} onClose={closeAlllPopup} isOpen={isImagePopupOpen ? 'popup_opened' : ''}/>

    </div>
  </CurrentUserContext.Provider>  
  );
}

export default App;
